import { NgModule, ModuleWithProviders } from '@angular/core';
import { ShortcutsService } from './shortcuts.service';

@NgModule({
  declarations: [],
  imports: [
  ],
  exports: []
})
export class ShortcutsModule {
  static forRoot(): ModuleWithProviders {
    return {
        ngModule: ShortcutsModule,
        providers: [
          ShortcutsService
        ]
    };
}
}
